:root {
  --color-red: #c80000;
  hyphens: auto;
  color: #fff;
  font-family: Work Sans, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
}

@media (width >= 768px) {
  :root {
    font-size: 20px;
  }
}

body {
  background-color: var(--color-red);
  margin: 0;
}

p {
  margin-top: 0;
  margin-bottom: .7em;
}

h1, h2, h3, h4 {
  hyphens: none;
  color: #000;
  margin-top: .9em;
  margin-bottom: .4em;
  font-weight: 700;
  line-height: 1.3;
}

h4 {
  letter-spacing: .1em;
  text-transform: uppercase;
  border-top: .2em solid #fff;
  padding-top: .5em;
  font-size: 1em;
}

h4 + h2 {
  margin-top: .1em;
}

.node-image:not(.header), .node-text, .node-grid {
  width: 94%;
  margin: 0 auto;
}

@media (width >= 768px) {
  .node-image:not(.header), .node-text, .node-grid {
    max-width: 40rem;
  }
}

.node-image.header {
  justify-content: end;
  align-items: center;
  display: flex;
}

.node-image.header img {
  max-width: 70em;
}

.node-page {
  padding-bottom: 6em;
}

.header + .node-text {
  background-color: var(--color-red);
  margin-top: max(-25vw, -17rem);
  padding-top: 1vw;
  position: relative;
}

img {
  width: 100%;
  display: block;
}

:root {
  --node-gird--count: 12;
  --node-grid-column--count: 1;
}

.node-grid {
  display: var(--grid--display, grid);
  grid-template-columns: repeat(var(--node-gird--count), 1fr);
  grid-column-gap: var(--grid--grid-column-gap, 1.5rem);
}

.node-grid-column {
  grid-column: span var(--node-grid-column--count);
}

.logo img {
  width: 40%;
}

@media (width >= 768px) {
  .logo img {
    width: 30%;
  }
}

a {
  color: #000;
  text-underline-offset: .3em;
  -webkit-text-decoration: underline #fff;
  text-decoration: underline #fff;
  text-decoration-thickness: .15em;
}

a:hover {
  color: inherit;
}

